            desc: `Fans Club! Star Dust holders work as a team to invite their friends to join Virtual Star Fans Club. The winning team gets a big reward and the fans get surprising benefits from the platform.`,
          },
          { 
            img: require('@/assets/game-2.jpg'),
            title: 'The Heat',
            desc: `Social Power! Star Dust holders work as a team to promote their Virtual Star. A Scoring engine will monitor all social Channels. The winning team gets a huge price.`,
