          },
        ],
        innerHeight: window.innerHeight, // 浏览器窗口的高度
        scrollTop: 0,  // 滚动条距离顶部高度
      }
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)    
    },
    methods: {
      handleScroll () {
        this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop
        setTimeout(()=>{ this.handleAnimation() }, 500)
      },  
      handleAnimation () {
        let el = document.querySelectorAll('.animation')
        el.forEach((item) => {
          let elTop = this.getElementTop(item)
          if( this.innerHeight + this.scrollTop > elTop ) {
            item.classList.add('moving')
          }else {
            item.classList.remove('moving')
          }
        })    
      },
      getElementTop (el) {
        var elTop = el.offsetTop
        el = el.offsetParent
        while( el != null ) {
          elTop += el.offsetTop
          el = el.offsetParent
        }
        return elTop
     }
    }
  }
