<template>
  <div>

    <!-- 页头 -->
    <header-nav container="container-fluid" ref="nav" />
    <!-- pc端 -->
    <div class="container-fluid d-none d-lg-block" style="margin-top: 156px;">
      <h2 class="tit">GALLERY</h2>
      <div class="row" id="box">
        <div class="col-2">
          <div id="leftPlace"></div>
          <div 
            class="left-bar" 
            :class="{ fixed: leftBarFixed }"
            :style="{ 
              width: leftBarFixed ? leftWidth+'px' : 'auto',
              top: leftBarFixed ? navHeight+'px' : 'auto',
            }">
            <div 
              class="item" 
              v-for="(item, index) in category"
              :key="index"
              :class="{ active: item.active }"
              @click="handleCategory(item)">
                <img :src="item.photo">
                <span>{{ item.name }}</span>
              </div>
          </div>
        </div>
        <div class="col-10">
          <div class="row">
            <div 
              class="col-3"
              style="margin-bottom: 40px;"
              v-for="(item, index) in listData"
              :key="index">
              <div class="img-box text-center"><img class="img-fluid" :src="item.img"></div>
              <h3 style="margin-top: 10px;">{{ item.title }}</h3>
              <p>{{ item.desc }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 移动端 -->
    <div class="container d-block d-lg-none" style="margin-top: 120px;">
      <h2 class="tit">GALLERY1</h2>
      <div class="row">
        <div 
          class="col-6"
          style="margin-bottom: 40px;"
          v-for="(item, index) in listData"
          :key="index">
          <div class="img-box text-center"><img class="img-fluid" :src="item.img"></div>
          <h3 style="margin-top: 10px;">{{ item.title }}</h3>
          <p>{{ item.desc }}</p>
        </div>
      </div>
    </div>
    <!-- 悬浮小工具 -->
    <div class="tool d-block d-lg-none">
      <span data-bs-toggle="offcanvas" data-bs-target="#offcanvasExample">screen</span>
      <span @click="handleTop()">top</span>
    </div>
    <!-- 侧边栏 -->
    <div class="offcanvas offcanvas-end" id="offcanvasExample">
      <div class="offcanvas-header">
        <h5 class="offcanvas-title">screen</h5>
        <button type="button" class="btn-close" data-bs-dismiss="offcanvas"></button>
      </div>
      <div class="offcanvas-body">
        <div class="flex">
          <button 
            type="button" 
            class="btn btn-secondary"
            data-bs-dismiss="offcanvas"
            v-for="(item, index) in category"
            :key="index"
            :class="{ active: item.active }"
            @click="handleCategory(item)"><img :src="item.photo"> {{ item.name }}</button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import HeaderNav from '@/components/HeaderNav.vue'
  import Gallery from '@/script/gallery'

  export default {
    name: 'Home',
    components: { HeaderNav },
    data() {
      return {
        listData: Gallery,
        category: [
          { id: 1, name: 'Alcyone', photo: require('@/assets/gallery/Alcyone.jpg'), active: false },
          { id: 2, name: 'Chara', photo: require('@/assets/gallery/Chara.jpg'), active: false },
          { id: 3, name: 'Seren', photo: require('@/assets/gallery/Seren.jpg'), active: false },
          { id: 4, name: 'Sidra', photo: require('@/assets/gallery/Sidra.jpg'), active: false },
          { id: 5, name: 'Vega', photo: require('@/assets/gallery/Vega.jpg'), active: false },
        ],
        scrollTop: 0,
        navHeight: 0,
        boxTop: 0,
        leftWidth: 0,  
        leftBarFixed: false   
      }
    },
    mounted () {
      this.init()
      window.addEventListener('scroll', this.handleScroll)    
      window.addEventListener("resize", this.init)
    },
    methods: {
      init(){
        this.navHeight = this.$refs.nav.getNavHeight()
        this.boxTop = this.getElementTop(document.getElementById('box'))
        this.leftWidth = document.getElementById('leftPlace').clientWidth
      },
      handleScroll () {
        this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop
        if(this.scrollTop+this.navHeight > this.boxTop) this.leftBarFixed = true 
        else this.leftBarFixed = false 
      },
      handleCategory(row) {
        if(row.active){
          row.active = false
          this.listData = Gallery
        }else{
          let res = Gallery.filter((item) => {
            return item.id == row.id
          })
          this.listData = res

          this.category.forEach((item, index) => {
            if(index === row.id-1) {
              item.active = true
            } else {
              item.active = false
            }
          })
        }
        this.handleTop()
      },
      handleTop(){
        window.scrollTo(0, 0)
      },
      getElementTop (el) {
        var elTop = el.offsetTop
        el = el.offsetParent
        while( el != null ) {
          elTop += el.offsetTop
          el = el.offsetParent
        }
        return elTop
      }
    }
  }
</script>

<style scoped lang="scss">
  
  .left-bar {
    border-radius: 12px; overflow: hidden; 
    &.fixed { position: fixed; bottom: 0; overflow-y: auto; }
    .item {
      padding: 8px 16px; background-color: rgba(255, 255, 255, .2); border-bottom: 1px solid #666; cursor: pointer;
      img { width: 40px; height: 40px; margin-right: 12px; border-radius: 100%; }
      &:last-child { border: 0; border-radius: 0 0 12px 12px; }
      &:hover { background-color: rgba(255, 255, 255, .25); }
      &.active { background-color: #666; }
    }
  }

  .img-box { 
    width: 100%; border-radius: 12px; overflow: hidden; 
    img { 
      transition: all .6s ease-out;
      &:hover { transform: scale(1.2); } 
    }
  }

  .tool {
    position: fixed; right: 30px; bottom: 30px; border: 1px solid rgba(255, 255, 255, 0.3); border-radius: 8px; background-color: #000; overflow: hidden;
    span { display: block; padding: 10px; font-size: 12px; text-transform: uppercase; text-align: center; cursor: pointer; border-bottom: 1px solid rgba(255, 255, 255, 0.3); transition: all .5s ease-out; 
      &:hover { color: #FF5555; } 
    }
    &:last-child { border: 0; } 
  }

  .offcanvas {
    width: 320px; background-color: rgba(255, 255, 255, 0.8);
    .offcanvas-header { color: #333; }
    .offcanvas-body {
      .flex {
        flex-wrap: wrap; justify-content: space-between;
      }
      button { 
        display: block; margin-bottom: 20px; width: 47%; text-align: left;
        img { margin-right: 4px; height: 24px;width: 24px; border-radius: 100%; }
      }
    }
  }

  @media (min-width: 992px) {
    .offcanvas { width: 400px; }
  }

</style>
