<template>
  <header>
    <nav id="nav" class="navbar fixed-top navbar-expand-lg navbar-dark" :class="{ 'nav-bg': isNavBg }">
      <div :class="container">
         <a class="navbar-brand" href="/"><img class="logo-img" src="@/assets/logo.png"></a>
        <button 
          class="navbar-toggler" 
          type="button" 
          data-bs-toggle="collapse" 
          data-bs-target="#navbarToggler" 
          aria-controls="navbarToggler" 
          aria-expanded="false" 
          aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse text-uppercase" id="navbarToggler">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li class="nav-item">
              <a class="nav-link" @click="scrollToSection('intro')">intro</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="scrollToSection('map')">roadmap</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="scrollToSection('games')">games</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="scrollToSection('team')">team</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="scrollToSection('faq')">faq</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="/#/gallery">gallery</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="https://metastars.gitbook.io/whitepaper" target="_blank">whitepaper</a>
            </li>
          </ul>
          <span class="navbar-text">
            <a href="https://discord.gg/KBSVhf53Q2" target="_blank"><img src="@/assets/faq.png"></a>
            <a href="https://twitter.com/metastars_live?s=21&t=rZB_SXiI6Tm0SHq_fxUd3g" target="_blank"><img src="@/assets/twitter.png"></a>
          </span>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  props: {
    container: {
      type: String,
      default: 'container'
    },
  },
  name: 'Header',
  data() {
    return {
      scrollTop: 0,  // 滚动条距离顶部高度
      navHeight: '',  // navbav的高度
      isNavBg: false  // navbav是否有背景颜色
    }
  },
  mounted () {
    this.navHeight = this.getNavHeight()
    window.addEventListener('scroll', this.handleScroll)   
  },
  methods: {
    handleScroll () {
      this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      this.isNavBg = this.scrollTop > this.navHeight ? true : false
    },  
    scrollToSection(id) {
      if(this.$route.path !== '/home') {
        this.$router.push(`/home`)
      }
      setTimeout(()=>{
        let section = document.getElementById(id)
        if (section) {
          section.scrollIntoView({
            block: 'start'
          })
        }
      }, 200)
    },
    getNavHeight(){
      return document.getElementById('nav').clientHeight
    }
  }
}
</script>

