  .nav-bg { background-color: #000; }
  .navbar {
    .navbar-nav {
      width: 100%; justify-content: flex-end; font-size: 18px; background-color: #000;
      .nav-link { margin-left: 20px; color: #fff; cursor: pointer; }
    }
    .navbar-text {
      display: flex;
      a { margin-left: 20px; }
      img { height: 40px; }
