
let clothes = 12        // 衣服套数
let num = 4            // 每套数量
let name = 'Alcyone'   // 姓名   
let id = 1             // 定义id
let list = []          // 数据列表

for (var i = 0; i < clothes*num; i++) {
  let obj = {}
  let current = Math.ceil((i+1)/num)
  obj.id = id
  obj.title = `${name} #${i+1}`
  obj.img = require(`@/assets/gallery/${name}/${name}-${ current }-${ i < num ? i+1 : (i+1)-((current-1)*num) }.jpg`)
  list.push(obj)
}


